var getMedal = function (win) {
    var text = '';
    switch (win) {
        case '1':
            text = '<i class="fa-solid fa-trophy px-sm-2 pr-1 gold"></i>';
            break;
        case '2':
            text = '<i class="fa-solid fa-trophy px-sm-2 pr-1 silver"></i>';
            break;
        case '3':
            text = '<i class="fa-solid fa-trophy px-sm-2 pr-1 bronze"></i>';
            break;
        default:
            break;
    }
    return text;
};
var tableWinner = function (data) {
    var result = [];
    data.forEach(function (item) {
        var div = document.createElement('div');
        div.className = 'col-lg-4 col-sm-6 py-3';
        var header = document.createElement('h3');
        header.className = 'h5 text-center';
        if (item.type === 'sumo') {
            header.textContent = ' ';
        }
        else if (item.type === 'truck') {
            header.textContent = '';
        }
        else if (item.type === 'king') {
            header.textContent = '-';
        }
        var list = document.createElement('ul');
        item.first.forEach(function (person) {
            var place = document.createElement('li');
            var medal = getMedal('1');
            place.innerHTML = "1 \u043C\u0435\u0441\u0442\u043E. ".concat(person, " ").concat(medal);
            list.appendChild(place);
        });
        item.second.forEach(function (person) {
            var place = document.createElement('li');
            var medal = getMedal('2');
            place.innerHTML = "2 \u043C\u0435\u0441\u0442\u043E. ".concat(person, " ").concat(medal);
            list.appendChild(place);
        });
        item.third.forEach(function (person) {
            var place = document.createElement('li');
            var medal = getMedal('3');
            place.innerHTML = "3 \u043C\u0435\u0441\u0442\u043E. ".concat(person, " ").concat(medal);
            list.appendChild(place);
        });
        div.appendChild(header);
        div.appendChild(list);
        result.push(div);
    });
    return result;
};
export default tableWinner;
